import React, { useContext, useEffect, useRef, useState } from 'react'
import { TranslationContext } from '../store/TranslationContext'
import AutoResizeTextarea from '../Components/UI/AutoResizeTextarea'
import { Message } from '../Components/Message'
import axios from 'axios'
import { Loader } from '../Components/Loader'
import { BrowserContext } from '../store/BrowserContext'

const ContactUs = () => {
    const {translate} =useContext(TranslationContext)
    const [message, setMessage] = useState('')
    const [loading, setLoading] = useState(false)
    const [data, setData] = useState(null)
    const [error, setError] = useState(null)
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const submitMessage=()=>{
        setLoading(true)
        axios.post(
            '/api/contact-us/',
            {name, email, message},
            {
                headers : {
                  "Content-Type": "multipart/form-data",
                }
              }
        ).then(response=>{
            setData(response.data)
            setLoading(false)
            setName('')
            setEmail('')
            setMessage('')
        }).catch(error=>{
            setError(error)
            setLoading(false)
        })
    }
    const buttonDisabled = loading || name.trim()==''|| email.trim()=='' || message.trim()==''
    const {setTitle, setDescription} = useContext(BrowserContext)
    useEffect(()=>{
        setTitle(`${translate('Contact us')} | ${translate('Ch7al Machya')}`) // Translate this
        setDescription(translate('Contact our team at Ch7al machya for any information we will reply to you in less than a day'))
    },[])
   return (
    <div className='container mw-500 p-0'>
        { data && <Message variant='success' removeAlert={()=>{setData(null)}}>{ data }</Message>}
        { error && <Message variant='danger' removeAlert={()=>{setError(null)}}>{ error.response.data.detail || error.message }</Message>}
        <form type='POST' className='card mx-2 mt-4 p-2 p-md-3 d-flex flex-column gap-3'>
            <h4>{translate('Contact us')}</h4>
            <input maxLength={100} value={name} onChange={e=>{setName(e.target.value)}} placeholder={translate('Name')} className='form-control'/>
            <input type='email' maxLength={100} value={email} onChange={e=>{setEmail(e.target.value)}} placeholder={translate('Email')} className='form-control' />
            <AutoResizeTextarea maxLength={1000} rows={5} value={message} onChange={e=>setMessage(e.target.value)} placeholder={translate('Message')} className='form-control'/>
            <button disabled={buttonDisabled} onClick={submitMessage} className='btn btn-outline-success d-flex gap-2 justify-content-center'>{translate('Submit')}{ loading && <Loader diam='23' />}</button>
        </form>
    </div>
  )
}

export default ContactUs